import React from 'react'

import { PageTemplate, Header, Footer } from '../..'
import { PostForm, PostList } from '../..'

const SamplePage = () => {
  return (
    <PageTemplate header={<Header />} footer={<Footer />}>
      <PostForm />
      <PostList limit={15} />
    </PageTemplate>
  )
}

export default SamplePage



