import React from 'react'

import { PageTemplate, Header, Hero, Footer, FeatureList } from '../..'

const HomePage = () => {
  return (
    <PageTemplate header={<Header />} hero={<Hero />} footer={<Footer />}>
      <FeatureList />
    </PageTemplate>
  )
}

export default HomePage



